<template>
  <div class="glide" id="home">
    <!-- 轮播轨道 -->
    <div class="glide__track" data-glide-el="track">
      <!-- 所有轮播容器 -->
      <div class="glide__slides">
        <!-- 单个轮播组件 -->
        <div class="glide__slide left">
          <!-- 轮播标题 -->
          <div class="slide-caption">
            <h1> ImakerLab </h1>
            <h3>
              安利群大学生创新实验室
            </h3>
            <h3>
              创新是第一生产力, 通过在实验室的努力你可以领先其他人
            </h3>
            <!-- 探索按钮 -->
            <button class="explore-btn">JoinUs</button>
          </div>
          <!-- 背景遮罩 -->
          <div class="backdrop"></div>
          <!-- 轮播图片 -->
          <img src="../../assets/images/homebgc.png" alt=""/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {};
</script>

<style scoped>
  @import "../../assets/css/style.css";

  .el-menu-demo {
    justify-content: end !important;
    -webkit-box-pack: end;
    display:-webkit-box
  }


</style>



